@keyframes NextButton {
    0% {
        transform: translate(0, 10px);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes ScaleIn {
    0% {
        transform: scale(0, 0);
    }
    100% {
        transform: scale(1, 1);
    }
}

@keyframes FadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes WaveIn {
    0% {
        opacity: 0;
        transform: translateY(10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

div.HoverToScale {
    transform: scale(1, 1);
    transform-origin: 70em 23em;
    transition: transform 0.1s;
}
div.HoverToScale:hover {
    transform: scale(1.15, 1.15);
}
div.NextButton {
    position: absolute;
    width: 13em;
    left: calc(100% - 26em);
    top: calc(100% + 25em);
    height: 5em;
    background-image: url(../img/gui/arrow.svg);
    background-repeat: no-repeat;
    background-size: contain;
    filter: drop-shadow(0 0 6px yellow);
    opacity: 0;
    animation: NextButton 0.5s cubic-bezier(0, 0, 0.1, 1.01) infinite alternate, FadeIn 1s ease 4s forwards;
}
div.NextButton:hover {
    animation-play-state: paused;
}

div.FlexHoverToScale {
    transform: scale(1, 1);
    transition: transform 0.1s;
}
div.FlexHoverToScale:hover {
    transform: scale(1.15, 1.15);
}
div.FlexNextButton {
    width: 6em;
    height: 5em;
    background-image: url(../img/gui/arrow.svg);
    background-repeat: no-repeat;
    background-size: contain;
    filter: drop-shadow(0 0 6px yellow);
    opacity: 0;
    animation: NextButton 0.5s cubic-bezier(0, 0, 0.1, 1.01) infinite alternate, FadeIn 1s ease 4s forwards;
    margin-top: 5em;
}
div.FlexNextButton:hover {
    animation-play-state: paused;
}

div.chats {
    display: flex;
    flex: 0.3 1;
    align-items: flex-end;
    margin-right: 12%;
    margin-top: 5%;
    margin-bottom: 0%;
}

div.infoPictureArea {
    margin-top: 5em;
    width: 100%;
    height: calc(100% / 13);
    position: absolute;
    background-size: contain;
    background-color: rgb(160, 134, 134);
    opacity: 0;
    animation: WaveIn 1s cubic-bezier(0.04, 0.83, 0.35, 1.58) 4s forwards;
}

div.infoTitle {
    display: flex;
    flex: 1 1;
    color: white;
    font-size: xxx-large;
    letter-spacing: 0.1em;
    text-shadow: 0 0 13px #ff6f6f;
    margin-bottom: 1em;
    align-items: flex-end;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: flex-end;
    justify-content: flex-start;
    width: 70%;
}

div.infocontent {
    flex: 1 1;
    color: white;
    font-size: 1.2em;
    letter-spacing: 0.1em;
    line-height: 1.5em;
    opacity: 0;
    text-shadow: -1.5px 1px 2px #ce8a8a;
    animation: WaveIn 1s ease-in 2s forwards;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 70%;
}
